import { Router } from '@angular/router';
import { Component, ElementRef, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormBuilder,FormGroup, Validators } from '@angular/forms';

import { MenuOptions } from 'src/app/app.component';
import { SecurityQuestionsService } from './securityQuestionsService';
import { TranslateService } from '@ngx-translate/core';
import { AlertController, MenuController } from '@ionic/angular';
import { PopoverContentPage } from '../popover-content/popover-content.page';
import { AuthHandlerProvider } from 'src/providers/auth-handler/auth-handler';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import * as HttpStatus from 'http-status-codes';
import { PopoverController } from '@ionic/angular';
import { SignUpServiceProvider } from './sign-up-service';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.page.html',
  styleUrls: ['./sign-up.page.scss'],
})
export class SignUpPage implements OnInit {
  @ViewChild("canvas") canvasEl: any;
  @ViewChild("captchInput") captchIn: ElementRef;
  signUpForm: FormGroup;
  isRegistered: boolean;
  isValidUser: boolean = false;
  private _CANVAS: any;
  private _CONTEXT: any;
  questions: any;
  mainCaptcha: any;
  socialPlatforms: any;
  isShowingLoader: boolean = false;
  canTryAgain: boolean = false;
  errorMsg: any;
  ShowIdentityReqError: boolean = false;
  ShowIdentityLengthError: boolean = false;
  textVal: any;
  str1: any = "one";
  str2: any = "two";
  str3: any = "seven";


  constructor(
    public formBuilder: FormBuilder,
    public securityQuesService: SecurityQuestionsService,
    public utils: UtilService,public translate: TranslateService,
    public logger: LoggerServiceProvider, public popOverCtrl: PopoverController,
    public signUpService: SignUpServiceProvider,public changeRef: ChangeDetectorRef,
    private alertCtrl: AlertController, private router: Router,
    private menuControl: MenuController,


  ) {
    let EMAIL_PATTERN = ConfigProvider.EMAIL_PATTERN;
    this.signUpForm= this.formBuilder.group({
      identityNumber: ["", [Validators.required, Validators.minLength(3)]],
      policyNo: ["", [Validators.required, Validators.minLength(3)]],
      captcha: ["", Validators.required],
      name: ["", [Validators.required, Validators.minLength(3)]],
      email: ["", [Validators.required, Validators.pattern(EMAIL_PATTERN)]],
      sourceofInfo: ["", Validators.required],
      securityQuestion: ["", Validators.required],
      securityAns: ["", [Validators.required, Validators.minLength(1)]],
      secureword: [
        "",
        [
          Validators.required,
          Validators.minLength(4),
          Validators.maxLength(10),
        ],
      ],
      terms_and_conditions: [{ value: true, disabled: false }],
    });
    this.isValidUser = false;
    this.isRegistered=false;
   
   }

  ngOnInit() {
    console.log("1");
    // this.initialiseCanvas()
    //this.initializeForm();
  }

  ionViewDidEnter() {
    this.menuControl.enable(true, 'first');
    this.utils.component.emit(MenuOptions.SIGN_UP);
  }

  /** this method is to get security questions -  adapter call */
  requestForSecurityQuestions = function  () {
    this.logger.trace("Security Questions--SignUp--start");
    if (this.utils.isDeviceOnLine()) {
      this.securityQuesService.getSecurityQuestions().then(
        (result) => {
          try {
            this.questions = result.responseJSON.array;
            this.signUpForm.controls["securityQuestion"].setValue(
              this.questions[0]["Type"],
              { onlySelf: true }
            );
            this.getSocialPlatformlist();
          } catch (e) {
            this.utils.dissmisProgressLoader();
            let errorMsg = this.translate.instant("generic_msgs.server_err");
            this.utils.showAlert("", errorMsg);
          }
        },
        (err) => {
          this.utils.dissmisProgressLoader();
          let errorMsg = this.utils.handleServiceException(err);
          this.utils.showAlert("", errorMsg);
        }
      );
    } else {
      let errorMsg = this.translate.instant("generic_msgs.network_err");
      this.utils.showAlert("", errorMsg);
    }
  };
  getSocialPlatformlist() {
    if (this.utils.isDeviceOnLine()) {
      this.securityQuesService.getSocialData().then(
        (res) => {
          try {
            if (res["responseJSON"] && res["status"] == 200) {
              this.socialPlatforms =
                res["responseJSON"]["registrationData"].socialPlatforms;
              this.signUpForm.controls["sourceofInfo"].setValue(
                this.socialPlatforms[0]["type"],
                { onlySelf: true }
              );
              this.utils.dissmisProgressLoader();
            } else {
              this.utils.dissmisProgressLoader();
              this.logger.error("GET Banks list: " + JSON.stringify(res));
              this.errorMsg = this.translate.instant("generic_msgs.server_err");
            }
          } catch (e) {
            this.isShowingLoader = false;
            this.canTryAgain = true;
            this.logger.error("get banks catch eror : " + JSON.stringify(e));
            this.errorMsg = this.translate.instant("generic_msgs.server_err");
          }
        },
        (err) => {
          this.isShowingLoader = false;
          this.errorMsg = this.utils.handleServiceException(err);
        }
      );
    } else {
      this.isShowingLoader = false;
      this.canTryAgain = true;
      this.logger.error("Get Banks generic_msgs.network_err");
      this.errorMsg = this.translate.instant("generic_msgs.network_err");
    }
  }

 /** this method is to signup the user after valid check */
 signUpUser = function  () {
  if (
    this.signUpForm.controls.name.valid &&
    this.signUpForm.controls.email.valid &&
    this.signUpForm.controls.securityAns.valid &&
    this.signUpForm.controls.terms_and_conditions.value &&
    this.signUpForm.controls.sourceofInfo.value &&
    this.signUpForm.controls.secureword.valid
  ) {
    if (this.utils.isDeviceOnLine()) {
      this.logger.trace("Post Registration Submit--SignUp--start");
      this.utils.showProgressLoader();
      var identityNo = this.signUpForm.controls.identityNumber.value;
      var policyNo = this.signUpForm.controls.policyNo.value;
      var name = this.signUpForm.controls.name.value;
      var email = this.signUpForm.controls.email.value;
      var sourceofInfo = this.signUpForm.controls.sourceofInfo.value;
      var securityQuestion = this.signUpForm.controls.securityQuestion.value;
      var securityAns = this.signUpForm.controls.securityAns.value;
      var secureword = this.signUpForm.controls.secureword.value;
      var receiveNotifications = "No";
      var terms_and_conditions = this.signUpForm.controls.terms_and_conditions
        .value
        ? "Yes"
        : "No";
      this.signUpService
        .postRegistartionSubmit(
          identityNo,
          policyNo,
          name,
          email,
          sourceofInfo,
          securityQuestion,
          securityAns,
          secureword,
          receiveNotifications,
          terms_and_conditions
        )
        .then(
          (res) => {
            try {
              this.utils.dissmisProgressLoader();
              this.isRegistered = true;
              if (
                res["responseJSON"] &&
                res["responseJSON"]["statusCode"] == HttpStatus.OK
              ) {
                this.errorMsg =
                  res["responseJSON"]["userMessage"] ||
                  this.translate.instant("Signup.reg_success");
                this.isSuccess = true;
                this.showDialog("", this.errorMsg);
              } else if (
                res["responseJSON"] &&
                res["responseJSON"]["statusCode"] == HttpStatus.ACCEPTED
              ) {
                this.errorMsg =
                  res["responseJSON"]["userMessage"] ||
                  this.translate.instant("Signup.reg_success");
                this.showDialog("", this.errorMsg);
              }
            } catch (e) {
              this.utils.dissmisProgressLoader();
              this.isRegistered = false;
              let errorMsg = this.translate.instant(
                "generic_msgs.server_err"
              );
              this.utils.showAlert("", errorMsg);
            }
          },
          (err) => {
            this.utils.dissmisProgressLoader();
            this.isRegistered = false;
            let errorMsg = this.utils.handleServiceException(err);
            this.utils.showAlert("", errorMsg);
          }
        );
    } else {
      let errorMsg = this.translate.instant("generic_msgs.network_err");
      this.utils.showAlert("", errorMsg);
    }
  }
};
  checkUser = function () {
    // this.isValidUser = true;
    // this.requestForSecurityQuestions();
    if (
      this.signUpForm.controls.identityNumber.valid &&
      this.signUpForm.controls.policyNo.valid &&
      this.signUpForm.controls.captcha.valid
    ) {
      /**Check enetered captcha and generated are same or not */
      if (
        this.mainCaptcha.trim() != this.signUpForm.controls.captcha.value.trim()
      ) {
        this.refreshCaptcha();
        this.utils.showAlert(
          "",
          this.translate.instant("Signup.captcha_error")
        );
        return;
      }
      this.logger.trace("Validate User--SignUp--start");
      if (this.utils.isDeviceOnLine()) {
        this.utils.showProgressLoader();
        var identityNo = this.signUpForm.controls.identityNumber.value.trim();
        var policyNo = this.signUpForm.controls.policyNo.value.trim();
        this.signUpService.postRegistartionCheck(identityNo, policyNo).then(
          (res) => {
            try {
              if (res["responseJSON"]) {
                if (
                  res["responseJSON"]["statusCode"] == HttpStatus.ACCEPTED ||
                  res["responseJSON"]["statusCode"] == HttpStatus.OK
                ) {
                  this.isValidUser = true;
                  this.requestForSecurityQuestions();
                } else {
                  this.refreshCaptcha();
                  this.utils.dissmisProgressLoader();
                  this.isValidUser = false;
                  let errorMsg =
                    res["responseJSON"]["message"] ||
                    res["responseJSON"]["userMessage"] ||
                    this.translate.instant("generic_msgs.server_err");
                  this.utils.showAlert("", errorMsg);
                }
              } else {
                this.refreshCaptcha();
                this.utils.dissmisProgressLoader();
                this.isValidUser = false;
                let errorMsg =
                  res["responseJSON"]["message"] ||
                  res["responseJSON"]["userMessage"] ||
                  this.translate.instant("generic_msgs.server_err");
                this.utils.showAlert("", errorMsg);
              }
            } catch (e) {
              this.refreshCaptcha();
              this.utils.dissmisProgressLoader();
              this.isValidUser = false;
              let errorMsg =
                res["responseJSON"]["message"] ||
                res["responseJSON"]["userMessage"] ||
                this.translate.instant("generic_msgs.server_err");
              this.utils.showAlert("", errorMsg);
            }
          },
          (err) => {
            //if any service error, or already existed user
            this.refreshCaptcha();
            this.utils.dissmisProgressLoader();
            this.isValidUser = false;
            let errorMsg = this.utils.handleServiceException(err); // adapter exception handler
            this.utils.showAlert("", errorMsg);
          }
        );
      } else {
        let errorMsg = this.translate.instant("generic_msgs.network_err");
        this.utils.showAlert("", errorMsg);
      }
    }
  };

  /**navigate to login page */
  navigateToLogin() {
    this.router.navigate(['/login-first']);
    //this.navCtrl.setRoot(LoginFirstPage, { direction: "forward" });
  };

  /** ON LOAD OF PAGE THIS METHOD WILL CALL */

  // eslint-disable-next-line @angular-eslint/use-lifecycle-interface
  ngAfterViewInit(){
    this._CANVAS = this.canvasEl.nativeElement;
    this._CANVAS.height = 48;
     this._CANVAS.width = 200;
    this.initialiseCanvas(); //TO generate captcha
  }
  initialiseCanvas() {
    if (this._CANVAS.getContext) {
      this.setupCanvas();
    }
  }

  /**generate captcha and set to signup page */
  setupCanvas() {
    var alpha = new Array(
      "A",
      "B",
      "C",
      "D",
      "E",
      "F",
      "G",
      "H",
      "J",
      "K",
      "M",
      "N",
      "P",
      "Q",
      "R",
      "S",
      "T",
      "U",
      "V",
      "W",
      "X",
      "Y",
      "Z",
      "a",
      "b",
      "c",
      "d",
      "e",
      "f",
      "g",
      "h",
      "j",
      "k",
      "m",
      "n",
      "p",
      "q",
      "r",
      "s",
      "t",
      "u",
      "v",
      "w",
      "x",
      "y",
      "z"
    );
    var i;
    var code = "";
    for (i = 0; i < 6; i++) {
      code = code + alpha[this.utils.getRandomInt()] + "";
    }
    this.mainCaptcha = code;
    this._CONTEXT = this._CANVAS.getContext("2d");
    // console.log(this._CONTEXT);
    this._CONTEXT.fillStyle = "white";
    this._CONTEXT.clearRect(0, 0, this._CANVAS.width, this._CANVAS.height);
    this._CONTEXT.font =  "15px Arial";
    this._CONTEXT.textAlign = "center";
    this._CONTEXT.style
    this._CONTEXT.fillText(code, 100, 30);
  }
  /**on click of refresh in signup page -  to re-generate new captcha */
  refreshCaptcha() {
    this.captchIn["_isFocus"] = false;
    this.signUpForm["controls"]["captcha"].reset();
    this.signUpForm["controls"]["captcha"].markAsUntouched();
    this.signUpForm["controls"]["captcha"].markAsPristine();
    this.signUpForm["controls"]["captcha"].updateValueAndValidity();
    this.setupCanvas();

  }

  /**To remove enterd captcha in text filed if not alpha */
  keyUpChecker(event: any) {
    let newValue = event.target.value;
    let regExp = new RegExp("[A-Za-z]");
    if (!regExp.test(newValue)) {
      event.target.value = newValue.slice(0, -1);
    }
  }
  // async openPopup(e: Event) {
  //   const popover = await this.popOverCtrl.create({
  //     component: PopoverContentPage,
  //     event: e,
  //     componentProps: { msg: this.str1 },
  //     cssClass: 'custom-popover'
  //   });

  //   await popover.present();

  // }
  async openPopup(e: Event) {
    const popover = await this.popOverCtrl.create({
      component: PopoverContentPage,
      showBackdrop: false,
      event: e,
      componentProps: { msg: this.str1},
      cssClass: 'custom-popover'
    });

    await popover.present();

  }
  async openPopup1(e: Event) {
    const popover = await this.popOverCtrl.create({
      component: PopoverContentPage,
      showBackdrop: false,
      event: e,
      componentProps: { msg: this.str2},
      cssClass: 'custom-popover'
    });

    await popover.present();

  }
  async openPopup2(e: Event) {
    const popover = await this.popOverCtrl.create({
      component: PopoverContentPage,
      showBackdrop: false,
      event: e,
      componentProps: { msg: this.str3 },
      cssClass: 'custom-popover',
    });

    await popover.present();

  }

  //This Methode will call when user will click on the Forgot Policy Number
  forgotPolicyNumber() {
    var identityNo = this.signUpForm.controls.identityNumber.value;
    if (!identityNo) {
      this.errorMsg = this.translate.instant("Signup.identityNo_req");
      this.showDialog("", this.errorMsg);
    } else {
      if (identityNo.length > 2) {
        //this.showDialog("", "Identity No should be greater than 2 characters");
        this.utils.showProgressLoader();
        var userRegistered = false; //User is not registered as of now so this field will be false.
        var mobileOs = "AOS";
        if (this.utils.getPlatform() == "ios") {
          mobileOs = "IOS";
        }
        this.signUpService
          .postForgotPolicyNumber(identityNo, userRegistered, mobileOs)
          .then(
            (res) => {
              try {
                if (
                  res["responseJSON"] &&
                  res["responseJSON"]["statusCode"] == HttpStatus.ACCEPTED
                ) {
                  this.errorMsg = res["responseJSON"]["userMessage"];
                  this.utils.dissmisProgressLoader();
                  this.showDialog("", this.errorMsg);
                }
              } catch (e) {
                this.utils.dissmisProgressLoader();
                this.isRegistered = false;
                let errorMsg = this.translate.instant(
                  "generic_msgs.server_err"
                );
                this.presentAlert("", errorMsg);
              }
            },
            (err) => {
              this.utils.dissmisProgressLoader();
              this.isRegistered = false;
              let errorMsg = this.utils.handleServiceException(err);
              this.presentAlert("", errorMsg);
            }
          );
      }
    }
  }

  /**
   *
   * @param title - this is the title of the alert
   * @param message  - message that need to display on the alert.
   */
  showDialog(title: string, message: string) {
    let buttons = [
      {
        text: "Ok",
        cssClass:'alertbutton'

        // handler: () => {
        //    this.navCtrl.pop();
        // }
      },
    ];

    // let alert = this.alertCtrl.create({
    //   title: title,
    //   message: message,
    //   buttons: buttons,
    //   enableBackdropDismiss: false,
    // });
   // return alert.present();
   const alert: any = this.alertCtrl.create({
    header: title,
    message: message,
    buttons: buttons
}).then((alert) => alert.present());
}


  async presentAlert(title: string, message: string, enableBackdropDismiss?: boolean, buttons?: Array<any>) {
  let alert = this.alertCtrl.create({
    message: title,
    buttons: ['ok']
  });
  (await alert).present();
}
}
